* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-family: Roboto, sans-serif;
  font-weight: 400;
}

body {
  margin: 0;
  padding: 0;
}

.product {
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
}

.product__photo {
  width: 100%;
  height: 134px;
  margin-bottom: 36px;
  padding: 0 3px;
}

.product__title {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.product__code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
}

.product__rank {
  justify-content: space-between;
  align-items: center;
  height: 16px;
  margin-bottom: 24px;
  display: flex;
}

.product__rank-reviews {
  color: #060b35;
  font-size: 10px;
  line-height: 14px;
}

.product .stars {
  gap: 4px;
  display: flex;
}

.product .stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.product .stars :nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.product__price {
  color: #616070;
  justify-content: space-between;
  align-items: center;
  height: 18px;
  margin-bottom: 16px;
  font-size: 12px;
  line-height: 18px;
  display: flex;
}

.product__price-value {
  color: #060b35;
  text-align: right;
  font-size: 16px;
  font-weight: 700;
}

.product__btn {
  text-align: center;
  color: #fff;
  text-transform: uppercase;
  background: #00acdc;
  border: none;
  border-radius: 5px;
  flex-shrink: 0;
  width: 100%;
  padding: 12px 70px 12px 69px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: inline-block;
}

.product__btn:hover {
  color: #00acdc;
  background: #fff;
  border: 1px solid #00acdc;
}

